.main-header__container {
  position: relative;
  padding: 10px 52px 16px 20px;
  border-bottom: 1px solid #e6e6e6;

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 27px 30px 61px;
    border: 0;
  }

  @media (min-width: @desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
    padding: 53px 40px 111px;
  }
}

.main-header__logo-block {
  display: flex;
  justify-content: space-between;
  align-items: center;

  & a:last-child {
    margin: 0 auto;
  }
}

.main-header__promo {
  position: relative;
  text-align: center;

  @media (min-width: @tablet-width) {
    text-align: inherit;
  }

  @media (min-width: @desktop-width) {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 110px;
  }
}
