.features {
  padding: 20px 0 50px;

  @media (min-width: @tablet-width) {
    padding: 46px 0 46px;
    border-bottom: 1px solid #e6e6e6;
  }

  @media (min-width: @desktop-width) {
    padding: 71px 0 26px;
    border-bottom: 0;
  }
}

.features__container {
  padding: 0 20px;

  @media (min-width: @tablet-width) {
    padding: 0 30px;
  }

  @media (min-width: @desktop-width) {
    max-width: 1300px;
    padding: 0 40px;
  }
}

.features__title {
  margin-bottom: 42px;

  @media (min-width: @tablet-width) {
    margin-bottom: 32px;
  }
}

.features__list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    row-gap: 33px;
    column-gap: 58px;
  }

  @media (min-width: @desktop-width) {
    column-gap: 80px;
  }
}

.features__item {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 60px;
  padding-left: 80px;

  @media (max-width: @mobile-width-only) {
    & br {
      display: none;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: @base-color;
    background-image: url("../img/leaf.svg");
    background-repeat: no-repeat;
    background-position: center;

    @media (min-width: @tablet-width) {
      top: 40px;
      width: 80px;
      height: 80px;
    }
  }

  &:nth-child(2)::before {
    background-image: url("../img/powder.svg");
  }

  &:nth-child(3)::before {
    background-image: url("../img/eat.svg");
  }

  &:nth-child(4)::before {
    background-image: url("../img/time.svg");
  }

  @media (min-width: @tablet-width) {
    &::after {
      content: "1";
      position: absolute;
      top: 0;
      right: 7px;
      font-size: 280px;
      line-height: 280px;
      color: #f2f2f2;
      z-index: -1;
    }

    &:nth-child(2)::after {
      content: "2";
      right: 0;
    }

    &:nth-child(3)::after {
      content: "3";
      right: 0;
    }

    &:nth-child(4)::after {
      content: "4";
      right: 0;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 153px 0 57px;

    &:nth-child(1) {
      width: 230px;
      padding-right: 29px;
    }

    &:nth-child(2) {
      width: 246px;
      padding-right: 19px;
    }

    &:nth-child(3) {
      width: 247px;
      padding-right: 38px;
    }

    &:nth-child(4) {
      width: 249px;
    }
  }

  @media (max-width: @mobile-width-only) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.features__description {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
  }
}
